import React, { Component } from 'react';

class Slideshow extends Component {
  slideInterval = 5000;
  slideShowInterval;
  // paused = false;

  constructor(props) {
      super(props);
      this.goToNextSlide = this.goToNextSlide.bind(this);
      this.goToPreviousSlide = this.goToPreviousSlide.bind(this);
      // this.handleDotClick = this.handleDotClick.bind(this);

      this.state = {
        menu_y: 0,
        index: 0,
        autoplay: true,
        translateValue: 0,
        paused: false,
      }
  }

  setInterval(interval) {
    this.slideShowInterval = window.setInterval(() => {
        if(!this.state.paused) {
          this.goToNextSlide();
        }
      }, interval);
  }
  
  componentDidMount() {
    if(this.state.autoplay) {
      this.setInterval(this.slideInterval);
    }
  }

  goToNextSlide() {
    this.setState({index: this.state.index + 1 >= this.props.images.length ? 0 : this.state.index + 1});
  }

  goToPreviousSlide() {
    this.setState(
      {
        // translateValue: this.state.translateValue + mainWindow.getSize()[0],
        index: this.state.index - 1 < 0 ? this.props.images.length-1 : this.state.index - 1
      });
  }

  render() {
    return (
      <div className='slideshow-container'>
        <div
          className='slideshow'
          style={{
            background: 'url('+ this.props?.images?.[this.state?.index || 0]?.name + ')',
            transform: this.props?.images?.[this.state?.index || 0]?.transform ? this.props?.images?.[this.state?.index || 0]?.transform : '',
            // backgroundRepeat: 'no-repeat',
            // backgroundSize: 'cover',
            // backgroundPosition: 'right bottom, left top',
            // backgroundPosition: 'center'
            /*background: url(`${this.props.images[this.state.index]}`)
            background-repeat: no-repeat;
            background-position: center center;
            fixed;
            -webkit-background-size: cover;
            -moz-background-size: cover;
            -o-background-size: cover;
            background-size: cover;*/
          }}
        />
        {/* Slider next/previous buttons */}
        <div
          className='slider-btn'
          style={{
            left: '10px',
            background: 'url(./slider-left-arrow.svg)',
            backgroundRepeat: 'no-repeat',
            backgroundSize: '70% 70%',
            backgroundPosition: '15px 10px'
          }}
          onClick={() => {
            window.clearInterval(this.slideShowInterval);
            this.setState( { paused: true });
            this.goToPreviousSlide();
            // this.setInterval(this.slideInterval);
          }}
        />

        {/* !this.state.paused && */}
        <div
          className='slider-pause-btn'
          style={{
            right: '46.5vw',
            background: !this.state.paused ? 'url(./slider-pause.svg)' : 'url(./slider-play.svg)',
            // backgroundRepeat: 'no-repeat',
            // backgroundSize: '80% 80%',
            // backgroundPosition: '300px 10px',
            // backgroundPosition: '7px 7px',
            // display: 'block',
            // margin: 'auto',
          }}
          onClick={() => {
            if(this.state.paused) {
              this.setInterval(this.slideInterval);
              this.setState({
                ...this.state,
                paused: false,
              });
            } else {
              window.clearInterval(this.slideShowInterval);
              // this.state.paused = true;
              this.setState({
                ...this.state,
                paused: true,
              });
              // this.state.paused = !this.state.paused;
              // this.setInterval(0);
            }
          }}
        />
        
        <div
          className='slider-btn'
          style={{
            right: '10px',
            background: 'url(./slider-right-arrow.svg)',
            backgroundRepeat: 'no-repeat',
            backgroundSize: '70% 70%',
            backgroundPosition: '15px 10px'
          }}
          onClick={() => {
            window.clearInterval(this.slideShowInterval);
            this.setState( { paused: true });
            this.goToNextSlide();
            // this.setInterval(this.slideInterval);
          }}
        />
        {/* Slider nav */}
        <div style={{marginTop: '-50px'}}>
          <div
            style={{
              display: 'inline-block',
              height: '25px',
              marginLeft: 'auto',
              marginRight: 'auto',
              marginBottom: '50px',
              borderRadius: '80px',
              border: '1px dashed #ff7200',
              backgroundColor: 'rgba(0,0,0,.5)'
            }}
          >
            <div style={{display: 'flex'}}>
              {
                this.props.images.map((img_path, index) =>
                (
                  <div
                    key={`slideshow-image-${index}`}
                    style={{
                      width: '17px',
                      height: '17px',
                      marginLeft: '2px',
                      marginRight: '2px',
                      marginTop: '3px',
                      borderRadius: '20px',
                      backgroundColor: index === this.state.index ? '#ff7200' : '#fff'
                    }}
                  />
                ))
              }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Slideshow;